.rightSideBar {
  display: grid;
  padding: 0 12px;
}

.rightContainer {
  width: 400px;
  padding: 6px 12px;
  position: fixed;
}

.searchBar {
  width: 100%;
  background-color: rgba(167, 207, 255, 0.075);
  border-radius: 21px;
  height: 42px;
  display: grid;
  grid-template-columns: max-content max-content max-content;
  justify-content: center;
  align-items: center;
  padding: 0 0 0 18px;
}

.searchBar > div {
  fill: #8ba2b1;
}

.searchBar > input {
  border: none;
  background-color: rgba(0, 0, 0, 0);
  height: 100%;
  width: 336px;
  padding: 12px;
  font-size: 16px;
}

.searchBar > input::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #8ba2b1;
  opacity: 1; /* Firefox */
}

.searchBar > input:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #8ba2b1;
}

.searchBar > input::-ms-input-placeholder {
  /* Microsoft Edge */
  color: #8ba2b1;
}

.searchBar > input:focus {
  outline: none;
}

.active {
  outline: 1px solid rgb(0, 192, 80);
}
