.buzz {
  width: 598px;
  height: fit-content;
  display: grid;
  grid-template-columns: max-content 1fr;
  padding: 12px 16px;
  column-gap: 12px;
  border-top: none;
  border-bottom: 1px solid rgba(148, 167, 197, 0.3);
}

.buzz > div:first-child {
  width: 48px;
  height: 48px;
  background-color: rgb(103, 115, 122);
  border-radius: 50%;
}

.profilePicture {
  width: 48px;
  height: 48px;
  border-radius: 50%;
}

.contentImage {
  border-radius: 16px;
  margin-top: 8px;
  outline: 1px solid rgba(76, 76, 76, 0.2);
}

.postInfo {
  display: grid;
  grid-template-columns: max-content max-content;
  gap: 4px;
}

.postInfo > p:first-child {
  font-size: 15px;
  font-weight: 600;
}

.postInfo > p:nth-child(2) {
  color: #8b98a5;
  font-size: 15px;
}

.postContent {
  display: grid;
  grid-template-rows: max-content max-content;
  gap: 6px;
}
